import { Component, OnInit } from '@angular/core';
import { Vendor } from 'src/app/model/vendor-details-model';
import { Router, ActivatedRoute } from '@angular/router';
import { AddVendorService } from 'src/app/service/add-vendor.service';
import { AuthService } from 'src/app/service/auth.service';

@Component({
  selector: 'app-add-vendor',
  templateUrl: './add-vendor.component.html',
  styleUrls: ['./add-vendor.component.scss']
})
export class AddVendorComponent implements OnInit {

  vendor = new Vendor();
  vId:any;

  constructor(private authService:AuthService,private router: Router,private route:ActivatedRoute,private _service: AddVendorService) { }

  ngOnInit() {
    this.getVendorById();
  }

  resetform() {
    this._service.vendor = {
      vendor_id: null,
      vendor_name: '',
      address: '',
      location: '',
      service: '',
      pincode: '',
      contact_id: '',
      name: '',
      department: '',
      email: '',
      phone: '',
      isActive: null
    }
  }


  saveVendor() {
    this.resetform();
    this._service.insertVendor(this.vendor)
    .subscribe((response)=>{
      
      console.log(response);
      // this.toastr.success('Saved successfully');
      this.router.navigate(['viewVendor'])
    }, (error)=>{
      console.log(error);
    });
  }

  getVendorById(){
    this.vId=this.route.snapshot.paramMap.get('id');
    this._service.getVendorbyId(this.vId)
    .subscribe((dat)=>{
      this.vendor=dat
      console.log(this.vendor);
      //console.log(vendor.department);
    },(error)=>{
      console.log(error);
    })
  }

  logout(){
    this.authService.logout();
  }
}
