import { Component, OnInit } from '@angular/core';
import { ViewVendorService } from 'src/app/service/view-vendor.service';
import { Router } from '@angular/router';
import { Vendor } from 'src/app/model/vendor-details-model';
import { AuthService } from 'src/app/service/auth.service';

@Component({
  selector: 'app-view-vendor',
  templateUrl: './view-vendor.component.html',
  styleUrls: ['./view-vendor.component.scss']
})
export class ViewVendorComponent implements OnInit {

  viewVendors: Vendor[];
  vendor = new Vendor();
  searchText;
  searchid: any;

  constructor(private authService:AuthService,private _viewVendorService: ViewVendorService,private router:Router) { }

  ngOnInit() {
    this.getVendor();
  }

  edit_Vendor(data:any){
    this.router.navigate(['addVendor',data]);
    
  }
  viewContact(id:any){
    this.router.navigate(['viewContact',id]);
  }
  addVendor() {
    this.router.navigate(['addVendor']);
};

  getVendor(): void{
    this._viewVendorService.getAllVendor().subscribe((vendorList)=>{
      this.viewVendors=vendorList,
      console.log(vendorList);
    },(error)=>{
      console.log(error);
    });
  
    
  }

  DisableVendor()
  {
    console.log("inside disable" +this.searchid);
    this._viewVendorService.disableVendor(this.searchid)
    .subscribe((data)=>{
      console.log(data);
      this.getVendor();
    },(error)=>{
      console.log(error);
    });   
  }
  onSelect(selectedVendor: any) {
    console.log("Selected item Id:", selectedVendor.vendor_id);
    this.searchid = selectedVendor.vendor_id;
  }

  logout(){
    this.authService.logout();
  }
}
