import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { ViewVendorComponent } from './vendor/view-vendor/view-vendor.component';
import { AddVendorComponent } from './vendor/add-vendor/add-vendor.component';
import { ViewContactDetailsComponent } from './vendor/view-contact-details/view-contact-details.component';
import { AuthGuard } from './guards/auth.guard';


const routes: Routes = [
  {
    path:'',
    redirectTo: 'login', pathMatch: 'full'
  },
  
  {
    //login Page
    
    path:'login',
    component:LoginComponent
  },
  {
    path:'viewVendor',
    component:ViewVendorComponent, canActivate: [AuthGuard]
  },
  {
    path:'addVendor/:id',
    component:AddVendorComponent, canActivate: [AuthGuard]
  },
  {
    path:'addVendor',
    component:AddVendorComponent, canActivate: [AuthGuard]
  },
  {
    path:'viewContact',
    component:ViewContactDetailsComponent, canActivate: [AuthGuard]
  },
  {
    path:'viewContact/:id',
    component:ViewContactDetailsComponent, canActivate: [AuthGuard]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
