import { Component, OnInit } from '@angular/core';
import { Vendor } from 'src/app/model/vendor-details-model';
import { ActivatedRoute } from '@angular/router';
import { ViewVendorService } from 'src/app/service/view-vendor.service';
import { AuthService } from 'src/app/service/auth.service';

@Component({
  selector: 'app-view-contact-details',
  templateUrl: './view-contact-details.component.html',
  styleUrls: ['./view-contact-details.component.scss']
})
export class ViewContactDetailsComponent implements OnInit {

  constructor(private authService:AuthService,private _service:ViewVendorService,private route:ActivatedRoute) { }

  ngOnInit() {

    this.getVendorById();
    this.resetform();

  }

  vId:any;
  vendor = new Vendor();
  

  resetform() {
    this._service.vendor = {
      vendor_id: null,
      vendor_name: '',
      address: '',
      location: '',
      service: '',
      pincode: '',
      contact_id: '',
      name: '',
      department: '',
      email: '',
      phone: '',
      isActive: null
    }
  }

  getVendorById(){
    this.resetform();
    this.vId=this.route.snapshot.paramMap.get('id');
    this._service.getVendorbyId(this.vId)
    .subscribe((data) =>{
      this.vendor=data;
      console.log('inside');
      console.log(data);
      
      console.log(this.vendor.vendor_name);
    },(error)=>{
      console.log(error);
    })
  }
  logout(){
    this.authService.logout();
  }
  
}
