import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { Vendor } from '../model/vendor-details-model';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ViewVendorService {
  vendor:Vendor;
  constructor(private _httpService: HttpClient,private router:Router) { }


  getAllVendor(): Observable<Vendor[]>{
    return this._httpService.get<Vendor[]>(environment.APIUrl+'vendor/null')
  }

  disableVendor(searchid: any): any {
    let body = JSON.stringify(searchid);
    let headers = new HttpHeaders({ 'Content-Type': 'application/json' });
    let options = { headers: headers }
    console.log("inside disable staff" + searchid);
    return this._httpService.put<Vendor[]>(environment.APIUrl + 'disablevendor/' + searchid, body, options);

  }

  getVendorbyId(vId:any): Observable<Vendor> {
    return this._httpService.get<Vendor>(environment.APIUrl + 'vendors/' + vId)
  }
}
